AAe;EACf,iBAAiB;EACjB,SAAS;AACX;;AAEA;EACE,eAAe;EACf,cAAc;EACd,aAAa;AACf;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,oBAAoB;AACtB;;AAEA,SAAS;AACT;EACE,kBAAkB;EAClB,cAAc;EACd,UAAU;EACV,yBAAyB;EACzB,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,WAAW;EACX,YAAY;EACZ,gBAAgB;EAChB,iBAAiB;EACjB,cAAc;EACd,yBAAyB;EACzB,yBAAyB;EACzB,kBAAkB;AACpB;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,oBAAoB;EACpB,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,oBAAoB;EACpB,iBAAiB;AACnB;;AAEA;EACE,qBAAqB;EACrB,YAAY;EACZ,YAAY;EACZ,cAAc;EACd,iBAAiB;EACjB,YAAY;EACZ,yBAAyB;EACzB,yBAAyB;EACzB,kBAAkB;AACpB;;AAEA;EACE,gEAAgE;EAChE,yBAAyB;AAC3B;;AAEA;;CAEC;AACD;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,yBAAyB;;EAEzB,eAAe;EACf,YAAY;EACZ,WAAW;EACX,kBAAkB;EAClB,SAAS;;EAET,eAAe;EACf,YAAY;EACZ,eAAe;AACjB","sourcesContent":["* {\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\nbody {\r\n  width: 100%;\r\n  height: 100%;\r\n  position: relative;\r\n}\r\n\r\n#main-resto_list {\r\n  max-width: 80%;\r\n  margin: 2rem auto;\r\n}\r\nbutton,\r\na,\r\ninput,\r\n.btn {\r\n  box-sizing: border-box;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n#loadingIndicator {\r\n  margin: 0 auto;\r\n}\r\n\r\n.lds-facebook,\r\n.lds-facebook div {\r\n  box-sizing: border-box;\r\n}\r\n.lds-facebook {\r\n  display: inline-block;\r\n  position: relative;\r\n  width: 80px;\r\n  height: 80px;\r\n}\r\n.lds-facebook div {\r\n  display: inline-block;\r\n  position: absolute;\r\n  left: 8px;\r\n  width: 16px;\r\n  background: currentColor;\r\n  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;\r\n}\r\n.lds-facebook div:nth-child(1) {\r\n  left: 8px;\r\n  animation-delay: -0.24s;\r\n}\r\n.lds-facebook div:nth-child(2) {\r\n  left: 32px;\r\n  animation-delay: -0.12s;\r\n}\r\n.lds-facebook div:nth-child(3) {\r\n  left: 56px;\r\n  animation-delay: 0s;\r\n}\r\n@keyframes lds-facebook {\r\n  0% {\r\n    top: 8px;\r\n    height: 64px;\r\n  }\r\n  50%,\r\n  100% {\r\n    top: 24px;\r\n    height: 32px;\r\n  }\r\n}\r\n\r\n.skip2content {\r\n  background-color: black;\r\n  border-radius: 4px;\r\n  color: black;\r\n  font-weight: 500;\r\n  left: 0;\r\n  line-height: 44px;\r\n  margin: 0 auto;\r\n  padding: 0 10px;\r\n  position: absolute;\r\n  right: 0;\r\n  top: -50px;\r\n  transition: 0.35s;\r\n  width: -webkit-max-content;\r\n  width: -moz-max-content;\r\n  width: max-content;\r\n  z-index: 1500;\r\n}\r\n\r\n.skip2content:focus {\r\n  top: 30px;\r\n}\r\n\r\nheader {\r\n  top: 0;\r\n  position: sticky;\r\n  z-index: 100;\r\n}\r\n\r\n/*\r\n  NAVBAR\r\n*/\r\n\r\n.app-bar {\r\n  padding: 8px 16px;\r\n  background-color: black;\r\n  display: grid;\r\n  grid-template-columns: auto 1fr auto;\r\n  gap: 10px;\r\n  position: sticky;\r\n  top: 0;\r\n  z-index: 99;\r\n  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.app-bar .app-bar__menu {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n#hamburgerButton {\r\n  color: white;\r\n}\r\n.app-bar .app-bar__menu button {\r\n  background-color: transparent;\r\n  border: none;\r\n  font-size: 18px;\r\n  padding: 8px;\r\n  cursor: pointer;\r\n}\r\n\r\n.app-bar .app-bar__brand {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.app-bar .app-bar__brand a {\r\n  color: #faf7f7;\r\n  text-transform: uppercase;\r\n  font-size: 1.8rem;\r\n  font-weight: bold;\r\n  user-select: none;\r\n}\r\n\r\n.app-bar .app-bar__navigation {\r\n  position: absolute;\r\n  top: 50px;\r\n  left: -180px;\r\n  width: 150px;\r\n  transition: all 0.3s;\r\n  padding: 8px;\r\n  background-color: black;\r\n  overflow: hidden;\r\n}\r\n\r\n.app-bar .app-bar__navigation.open {\r\n  left: 0;\r\n}\r\n\r\n.app-bar .app-bar__navigation ul li a {\r\n  display: inline-block;\r\n  text-decoration: none;\r\n  color: white;\r\n  padding: 8px;\r\n  margin-bottom: 5px;\r\n  width: 100%;\r\n  text-transform: uppercase;\r\n}\r\n\r\n/* \r\n  hero\r\n*/\r\n\r\n.hero-container {\r\n  min-height: 380px;\r\n  position: relative;\r\n  background-image: url(\"../public/images/sharp/hero-image_4-small.jpg\");\r\n  width: 100%;\r\n  background-position: center;\r\n  background-repeat: no-repeat;\r\n  background-size: cover;\r\n}\r\n\r\n.hero-container .hero-content-container {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  text-align: center;\r\n}\r\n\r\n.hero-container .hero-content-container > h1,\r\np {\r\n  font-weight: bold;\r\n  line-height: 2em;\r\n}\r\n.hero-text {\r\n  text-align: center;\r\n  color: white;\r\n}\r\n\r\n/*\r\n  Main Content\r\n*/\r\n\r\nsection {\r\n  margin-top: 40px;\r\n}\r\n\r\na,\r\np,\r\nh1,\r\nh2,\r\nh3,\r\nh4 {\r\n  text-decoration: none;\r\n}\r\n\r\n.main-resto_container {\r\n  margin: 60px auto;\r\n}\r\n\r\n.main-resto_label {\r\n  margin: auto;\r\n  width: max-content;\r\n  font-size: 22px;\r\n}\r\n\r\n.main-resto_label hr {\r\n  margin-top: 0;\r\n  width: 50%;\r\n  border-bottom: 1px solid #9ac8cd;\r\n}\r\n\r\n.list-resto {\r\n  margin-top: 2rem;\r\n  text-align: left;\r\n  display: grid;\r\n  grid-gap: 16px;\r\n}\r\n\r\n.resto-item {\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  width: 100%;\r\n  border-radius: 15px;\r\n  overflow: hidden;\r\n  position: relative;\r\n  top: 0;\r\n  transition: top ease 0.4s;\r\n  cursor: pointer;\r\n}\r\n\r\n.resto-item:hover {\r\n  top: -4px;\r\n}\r\n\r\n.resto-title {\r\n  display: inline-block;\r\n  justify-content: space-between;\r\n}\r\n\r\n.resto-item_image {\r\n  object-fit: cover;\r\n  width: 100%;\r\n  height: 250px;\r\n}\r\n\r\n.resto-item_name {\r\n  font-weight: bold;\r\n  font-size: 24px;\r\n  margin-top: 6px;\r\n  transition: 0.3s opacity;\r\n  padding: 0 16px 0 16px;\r\n  text-align: center;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.resto-item_city {\r\n  position: absolute;\r\n  text-transform: uppercase;\r\n  top: 8px;\r\n  left: 16px;\r\n  border-radius: 5px;\r\n  background-color: #0e46a3;\r\n  padding: 5px 10px 5px;\r\n  color: #e1f7f5;\r\n  font-weight: 500;\r\n}\r\n\r\n.resto-item_rating {\r\n  color: black;\r